import React from 'react';
import ReactDOM from 'react-dom';
import styles from './searchbar.css';
import axios from 'axios';
import CytoscapeComponent from 'react-cytoscapejs';

class SearchBar extends React.Component {

    constructor(props) {
      super(props);
      this.state = { searchword: '', prevword: '', results: [], searching: false, persons:[] };
      this.elements = [
     ];
    }
    
    
    mySubmitHandler = (event) => {
      if(document.getElementById("searchquery").value!==""){
        //this.setState({searchword: document.getElementById("searchquery").value});
        this.state.searchword = document.getElementById("searchquery").value;
        var element = document.getElementById("display");
        element.classList.add("up");
        this.DBSearch();
        //this.state.prevword=event.target.value;
      }
    }
    
    
    mySizeHandler = (event) =>{
        if((event.target.value == "" && this.state.prevword !="") || (event.target.value != "" && this.state.prevword =="")){
            var element = document.getElementById("searchquery");
            element.classList.toggle("fullength");
            this.state.prevword=event.target.value;
        }
    }

    insertElements(searchTree){
      
      var coord=[[100,50]];

      for(var i=0;i<searchTree.length-1;i++)
      {
          if(searchTree[i+1][0].tree_index-searchTree[i][0].tree_index==1)
             coord.push( [coord[i][0]+60,coord[i][1]] );
          else
            {
               coord.push( [ (coord[i][0] + coord[Math.max(i-1, 0)][0])/2, coord[i][1]+100] );
            }
      }

      for(var i=0; i<searchTree.length;i++){
        this.cy.add(
          { data: { id: searchTree[i][0].tree_index, label:searchTree[i][0].search_word}, position: { x: coord[i][0], y: coord[i][1]} }
          );
      }
      for(var i=1; i<searchTree.length;i++){
        this.cy.add(
          { data: {source: searchTree[i-1][0].tree_index, target: searchTree[i][0].tree_index, label: `Edge from ${searchTree[i-1][0].tree_index} to ${searchTree[i][0].tree_index}` }  }
          );
      }
    }

    
    DBSearch(){
      if(document.getElementById("searchquery").value=="")
       return;
      this.state.searching = true;
      //this.cy.removeData();
      axios.get(`/search?searchquery=${this.state.searchword}`,{headers: {
        "Content-Type": "application/json"}
    })
      .then(res => {
        const results = res.data.result;
        const searchTree = res.data.searchTree;
        console.log(results);
        this.setState({results});
        //console.log(this.cy.data());
        

        this.insertElements(searchTree);
        
        
        this.state.searching = false;
      })

    }
    
    
    search_bar(){

        return (<div id="display" class="Hotbg">
        <input id="searchquery" type="text" class="Hotbg-txt" placeholder="Search >>>" onChange={this.mySizeHandler} onSubmit={this.mySubmitHandler}/>
        <a href="#" class="Hotbg-btn" onClick={this.mySubmitHandler}>
          <i class="fa fa-search" ></i>
        </a>
       </div>);
    }

    
    render() {
        
            // X.map(f) => [f(0.01) f(0.2) f(0.3)]
        return <div>
            {this.search_bar()}
          { this.state.results.map(
            function(result){
              return (
                      <div class='tile'>
                      
                      <div class ='tile-info'>
                        <h1>{result.search_word}</h1>
                        <h3 ><a class='tile-title' href={result.url}>{result.title}</a></h3>
                        <p>{result.description}</p>
                      </div>
                    </div>);
          }
          )}
          <div class="rightBoard">
            <CytoscapeComponent cy={(cy) => { this.cy = cy; }} elements={this.elements} style={ { width: '400px', height: '400px' } } />
          </div>
          </div>;
        
    }
    
}

export default SearchBar;